<template>
  <section>
    <Navbar navText="风采大赛" />
    <b-container fluid class="files-container">
      <div class="sxd-nav">
        <router-link to="/">
          <img src="data:image/png;base64,">
          首页
        </router-link>
        <span> /</span>
        <img src="data:image/png;base64,">
          <span>风采大赛</span>
      </div>
      <b-card-group deck>
        <b-card
          img-src="https://placekitten.com/380/200"
          img-alt="Image"
          img-top
          :footer=item.title
          footer-tag="footer"
          @click="postCid(item)"
          :key=item.id
          class="item"
          v-b-modal.modal-scrollable
          v-for="item in whcpList"
          :cid="item.id"
          no-body
        >
          <!-- <b-button href="#" variant="primary">{{item.title}}</b-button> -->
        </b-card>
      </b-card-group>
      <!-- <div @click="postCid(item)" :key=item.id class="item" v-b-modal.modal-scrollable v-for="item in whcpList" :cid="item.id">
        {{item.title}}
      </div> -->
    </b-container>
    <b-modal id="modal-scrollable" scrollable :title="title" ok-only ok-title="阅读完毕">
      <div v-if="type==='pdf'">
        <iframe src="https://www.phoca.cz/demo/phocadownload/phocapdf-demo.pdf" width="100%" height="500px">
              This browser does not support PDFs. Please download the PDF to view it: <a href="/test.pdf">Download PDF</a>
        </iframe>
      </div>
      <div v-if="type==='bzhan'">
        <b-embed
          type="iframe"
          aspect="16by9"
          src="//player.bilibili.com/player.html?aid=44211901&cid=77441895&page=1"
          allowfullscreen
        />
      </div>
      <div v-if="type==='mp4'">
        <b-embed
          type="iframe"
          aspect="16by9"
          src="https://www.youtube.com/embed/zpOULjyy-n8?rel=0"
          allowfullscreen
        />
      </div>
    </b-modal>
    <Footer/>
  </section>
</template>

<script>
import Navbar from '@/components/navbar'
import Footer from '@/components/footer'
export default {
  components: {
    Navbar,
    Footer
  },
  data () {
    return {
      whcpList: [
        {
          id: 111,
          title: 'pdf实例',
          content: 'https://www.phoca.cz/demo/phocadownload/phocapdf-demo.pdf',
          type: 'pdf'
        },
        {
          id: 222,
          title: '视频实例（B站视频实例）',
          content: '<iframe src="//player.bilibili.com/player.html?aid=44211901&cid=77441895&page=1" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true"> </iframe>',
          type: 'bzhan'
        },
        {
          id: 333,
          title: '视频实例（自身视频）',
          content: 'http://www.w3school.com.cn/i/movie.mp4',
          type: 'mp4'
        }
      ],
      content: '',
      title: '',
      type: ''
    }
  },
  methods: {
    postCid (e) {
      this.content = e.content
      this.title = e.title
      this.type = e.type
    }
  }
}
</script>

<style scoped>
.item {
  padding: 0.5rem 1.25rem;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.125);
  /* border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem; */
}
.files-container {
  padding-right: 0px;
  padding-left: 0px;
  /* background-color: rgb(246, 41, 20); */
  min-height: 87vh;
  width: 100vw;
}
.item:first-child {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
}

.item:first-child a {
  color: #000;
  font-weight: bolder;
  width: 3rem;
  text-align: center;
  margin-right: -1rem;
}
.sxd-nav {
  background-color: #fff;
  /* width: 100vw; */
  padding-left: 0.5rem;
  height: 2.8rem;
  display: flex;
  align-items: center;
  border-bottom: solid 1.4px rgba(0, 0, 0, 0.4);
}
.sxd-nav img {
  margin: -0.3rem 0 0 0.3rem;
}
.sxd-nav a {
  color: rgb(246, 41, 20);
}
.sxd-nav span {
  display: inline-flex;
  margin-left: 0.4rem;
}
.card {
  margin: 0.5rem;
  padding: 0;
}
footer {
  color: #000;
}
</style>



